@import "varialble";
@import "function";

.container__footer {}

.footer {
  height: 251px;
  background: $bg-clr;
  border-top: 1px solid #FFF;
  box-sizing: border-box;
  @include desktopSmall {
    height: 187px; }
  @include tabletSmall {
    height: 380px; }
  @include mobile {
    height: 503px; } }

.footer__foot-content {
  margin-top: 45px;
  @include desktopSmall {
    margin-top: 26px; } }

.foot-content {
  height: 89px;
  padding-left: 40px;
  box-sizing: border-box;
  @include desktopSmall {
    height: 85px;
    padding-left: 0; }
  @include tablet {
    padding-left: 20px; }
  @include tabletSmall {
    display: flex;
    flex-wrap: wrap;
    height: auto;
    justify-content: space-between; } }

.foot-content__logo {
  display: block;
  height: 56px;
  svg {
    @include desktopSmall {
      height: 44px; }
    @include tabletSmall {
      height: 56px; } }
  @include tabletSmall {
    width: 100%;
    text-align: center; } }

.foot-content__block {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  @include tabletSmall {
    width: 143px;
    margin-top: 25px;
    min-height: 124px;
    &:nth-child(4) {
      width: 220px;
      min-height: 90px; } } }

.foot-content__item {
  @include fontFSC($goth-m, 14px, $clr);
  text-transform: uppercase;
  line-height: 13px;
  @include desktopSmall {
    font-size: 12px;
    line-height: 11px; }
  @include tabletSmall {
    line-height: 20px; } }

.foot-content__item_no-upper {
  text-transform: none; }

.foot-content__img {
  margin-right: 10px;
  vertical-align: baseline; }

.footer__basement {
  margin-top: 50px;
  @include desktopSmall {
    margin-top: 37px; } }

.basement {
  @include tabletSmall {
    display: flex;
    flex-wrap: wrap; } }

.basement__payments {
  width: 208px;
  @include desktopSmall {
    width: 140px; }
  @include tabletSmall {
    width: 100%;
    justify-content: center; } }

.basement__item {
  line-height: 25px;
  @include desktopSmall {
    line-height: 20px;
    &:nth-child(1) {
      svg {
        width: 20px; } }
    &:nth-child(2) {
      svg {
        width: 20px; } }
    &:nth-child(3) {
      svg {
        width: 35px; } }
    &:nth-child(4) {
      svg {
        width: 35px; } } }
  @include tabletSmall {
    line-height: 25px;
    margin-left: 20px;
    &:nth-child(1) {
      margin-left: 0;
      svg {
        width: 29px; } }
    &:nth-child(2) {
      svg {
        width: 30px; } }
    &:nth-child(3) {
      svg {
        width: 45px; } }
    &:nth-child(4) {
      svg {
        width: 45px; } } } }

.basement__rights {
  br {
    display: none; }
  @include fontFSC($goth-r, 14px, $clr);
  line-height: 20px;
  @include desktopSmall {
    font-size: 12px;
    line-height: 11px; }
  @include tabletSmall {
    margin-top: 30px;
    width: 134px;
    line-height: 20px;
    //margin-right: 265px
    br {
      display: inline; } }
  @include mobile {
 } }    //margin-right: 150px

.basement__by {
  width: 208px;
  text-align: right;
  @include desktopSmall {
    width: 40px; }
  @include tabletSmall {
    width: 60px;
    margin-top: 36px; } }
