@import "sass/varialble";
@import "sass/function";

.container__contacts {}

.contacts {
  min-height: 613px;
  @include desktopSmall {
    display: flex;
    flex-direction: column;
    justify-content: flex-start; } }

.contacts__wrap {
  @include desktopSmall {
    display: flex;
    justify-content: space-between; }
  @include tabletSmall {
    flex-direction: column; } }

.contacts__cont-info {}

.cont-info {
  width: 100%; }

.cont-info__table {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 150px;
  max-width: 553px;
  justify-content: space-between;
  @include desktopBig {
    max-width: 700px; }
  @include desktopSmall {
    height: auto;
    flex: 1;
    max-width: 100%; } }

.cont-info__item {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  width: 48%;
  @include desktopBig {
    width: 40%; }
  @include desktopSmall {
    max-width: 230px; }
  @include tabletSmall {
    max-width: 100%;
    margin-top: 20px;
    width: 100%; } }

.cont-info__name {
  @include fontFSC($goth-m, 16px);
  line-height: 20px;
  @include desktopSmall {
    font-size: 14px; } }

.cont-info__value {
  @include fontFSC($goth-m, 16px);
  line-height: 20px;
  max-width: 163px;
  @include desktopSmall {
    font-size: 14px; } }

.cont-info__button {
  margin-top: 45px; }

.cont-info__soc-head {
  margin-top: 45px;
  @include desktopSmall {
    display: none; } }

.cont-info__socials {
  margin-top: 27px;
  max-width: 103px;
  @include desktopSmall {
    display: none; } }

.cont-info__social {}

.contacts__map {
  margin-right: -65px;
  @include desktopBig {
    margin-right: -30px; }
  @include desktopSmall {
    margin-top: 50px; } }

.map {
  width: 625px;
  height: 568px;
  @include desktopBig {
    width: 100%; }
  @include desktopSmall {
    width: 100%;
    margin-bottom: 50px; }
  @include tabletSmall {
    height: 418px; } }
