@import "varialble";
@import "function";

.container__side-nav {
  flex: none; }

.side-nav {
  width: 195px;
  background-color: $bg-clr;
  @include desktopSmall {
    width: 244px;
    min-height: 780px;
    padding: 65px 30px 47px 23px;
    box-sizing: border-box;
    border: 1px solid #fff; } }

.side-nav__item {
  margin-top: 19px;
  &:first-child {
    margin-top: 0; }
  .side-nav__item-line {
    cursor: pointer;
    .side-nav__item-img {
      width: 30px;
      height: 21px;
      display: block;
      path {
        fill: #fff; } }
    .side-nav__item-name {
      @include fontFSC($goth-b, 14px);
      line-height: 20px;
      width: 100%;
      padding: 0 10px;
      box-sizing: border-box;
      transition: all 0.2s ease-in-out; }
    .side-nav___arrow {
      height: 20px; } }
  .side-nav__sub-items {
    display: none;
    transition: all 0.2s ease-in-out;
    .side-nav__sub-item {
      cursor: pointer;
      transition: all 0.2s ease-in-out;
      &:hover {
        color: $orange; } } }
  svg {
    path {
      transition: all 0.2s ease-in-out; } }
  &:hover {
    .side-nav__item-name {
      color: $orange; }
    .side-nav__item-img {
      path {
        fill: $orange; } }
    svg {
      path {
        fill: $orange; } } } }

.side-nav__item_active {
  .side-nav__item-line {
    .side-nav__item-img {
      path {
        fill: $orange; } }
    .side-nav__item-name {
      color: $orange; }
    .side-nav___arrow {
      path {
        fill: $orange; } } }
  .side-nav__sub-items {
    display: block;
    margin-top: 25px;
    margin-bottom: 25px;
    padding: 0 30px;
    box-sizing: border-box;
    .side-nav__sub-item {
      @include fontFSC($goth-r, 14px);
      line-height: 13px;
      margin-top: 15px;
      &:first-child {
        margin-top: 0; } } } }







