@import "sass/varialble";
@import "sass/function";

.container__menu {}

.menu {}

.menu__header {
  @include mobile {
    flex-direction: column;
    justify-content: flex-start; } }

.menu__location {}

.menu__delivery {
  @include desktopSmall {
    flex-direction: column;
    justify-content: space-between;
    min-height: 76px;
    width: 223px; }
  @include mobile {
    margin-top: 25px;
    width: 100%;
    justify-content: flex-start;
    min-height: 0; } }

.menu__ways-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: 12px;
  margin-top: -5px;
  svg {
    margin-left: 15px;
    &:nth-child(1) {
      margin-left: 0; }
    &:nth-child(2) {
      margin-top: -9px; } }
  @include desktopSmall {
    margin-top: 15px;
    margin-left: 0;
    svg {
      margin-left: 0;
      margin-bottom: 17px;
      &:nth-child(1) {
        width: 93px; }
      &:nth-child(2) {
        width: 61px;
        margin-left: 15px; }
      &:nth-child(3) {
        width: 89px; }
      &:nth-child(4) {
        width: 103px;
        margin-left: 15px; } } }
  @include mobile {
    margin-top: 10px;
    svg {
      margin-left: 0;
      margin-bottom: 17px;
      &:nth-child(1) {
        width: 93px; }
      &:nth-child(2) {
        width: 61px;
        margin-left: 40px;
        margin-right: 50px; }
      &:nth-child(3) {
        width: 89px;
        margin-right: 30px; }
      &:nth-child(4) {
        width: 103px;
        margin-left: 15px; } } } }

.menu__delivery-text {
  @include fontFSC($goth-m, 16px);
  @include mobile {
    text-align: center; } }

.menu__body {
  margin-top: 32px;
  display: flex;
  min-height: 951px;
  @include desktopSmall {
    position: relative;
    margin-top: 8px; }
  @include mobile {
    margin-top: 25px;
    display: flex;
    flex-direction: column;
    position: static; } }

.menu__drop-down {
  display: none;
  @include desktopSmall {
    display: flex;
    position: absolute;
    top: -42px;
    left: 0; }
  @include mobile {
    position: static;
    width: 160px;
    margin: 0 auto; } }

.menu__drop-text {
  @include fontFSC($goth-b, 16px);
  line-height: 15px; }

.menu__drop-arrow {
  margin-left: 12px;
  height: 15px; }

.menu__side-nav {
  @include desktopSmall {
    position: absolute;
    display: none; }
  @include mobile {
    top: 0;
    left: 0;
    z-index: 100; } }

.menu__products {
  @include mobile {
    margin-top: 25px; } }

.products {
  padding: 0 0 0 30px;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  flex: auto;
  @include desktopSmall {
    display: flex;
    padding: 0; } }

.products__card {
  margin: 0 15px 20px 0;
  @include tabletSmall {
    margin: 0 auto 20px; } }
