.container {
  background: url("../img/main/bg.jpg");
  background-size: cover;
  position: relative; }

.container_no-bg {
  background: none; }

.container__fixed {
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
  @include desktopBig {
    max-width: 1400px; }
  @include desktopLarge {
    max-width: 1575px; }
  @include desktopSmall {
    max-width: 890px; }
  @include tablet {
    max-width: 100%; } }

.layout {
  display: flex;
  justify-content: space-between; }

.container__heading {
  @include fontFSC($goth-b, 48px);
  text-align: center;
  margin-top: 52px;
  line-height: 50px;
  @include desktopSmall {
    font-size: 36px;
    margin-top: 40px; }
  @include tabletSmall {
    font-size: 24px;
    line-height: 30px;
    width: 315px;
    margin: 40px auto 0; } }

.container__title {
  @include fontFSC($goth-bl, 36px);
  text-align: left;
  text-transform: uppercase;
  line-height: 34px;
  margin-top: 45px;
  @include desktopSmall {
    font-size: 24px;
    line-height: 23px; } }

.container__location {
  @include fontFSC($goth-r, 14px, $clr);
  text-align: left;
  line-height: 20px;
  cursor: pointer; }

.container__back {
  @include fontFSC($goth-r, 14px, $clr);
  line-height: 20px;
  margin-top: 22px;
  cursor: pointer;
  &:before {
    content: url("../img/main/back.svg");
    display: inline-block;
    height: 15px;
    margin-right: 16px;
    vertical-align: sub; }
  .container__back-text {
    display: inline-block; } }

.container__img {
  width: 100%; }

.button {
  display: block;
  border-radius: 20px;
  background: linear-gradient(to right, #E5E5E5 0%, rgba(229, 229, 229, 0) 100%);
  border: none;
  height: 34px;
  width: 215px;
  box-sizing: border-box;
  padding: 2px;
  cursor: pointer;
  transition: all 200ms ease-in-out;
  &:hover {
    background: $orange;
    .button__inner {
      background: $orange;
      @include fontFSC($goth-m, 16px); } } }

.button__inner {
  display: block;
  border-radius: 40px;
  background-color: $bg-clr;
  border: none;
  height: 100%;
  width: 100%;
  text-align: center;
  line-height: 30px;
  @include fontFSC($goth-m, 16px, $orange);
  text-transform: uppercase;
  cursor: pointer;
  transition: all 200ms ease-in-out; }

.button_long {
  width: 262px; }

.button-org {
  display: block;
  background-color: $orange;
  border: none;
  height: 35px;
  width: 185px;
  text-align: center;
  line-height: 35px;
  @include fontFSC($goth-m, 14px);
  text-transform: uppercase;
  box-sizing: border-box;
  padding: 0;
  cursor: pointer;
  transition: all 200ms ease-in-out;
  &:hover {
    background: #CD070F; }
  @include tabletSmall {
    width: 232px; } }

.input-field {
  display: block;
  height: 55px;
  width: 367px;
  box-sizing: border-box;
  padding: 18px 24px;
  background-color: transparent;
  border: 1px solid #C1BBBB;
  outline: none;
  @include fontFSC($goth-r, 19px);
  &:focus {
    border: 1px solid $orange; } }

.input-field_area {
  height: 127px;
  resize: none; }

.link {
  @include fontFSC($goth-m, 14px, $clr);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  div {
    transition: all 0.2s ease-in-out; }
  svg {
    transition: all 0.2s ease-in-out;
    path {
      transition: all 0.2s ease-in-out; } }
  &:hover {
    color: $orange;
    div {
      color: $orange; }
    svg {
      fill: $orange;
      path {
        fill: $orange; } } } }

.radio-wrap {
  display: block;
  position: relative;
  padding-left: 40px;
  padding-right: 20px;
  margin-bottom: 12px;
  @include fontFSC($goth-r, 16px);
  line-height: 25px;
  cursor: pointer;
  user-select: none;
  .radio {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    &:checked ~ .radio-label {
      &::after {
        display: block; } } }
  .radio-label {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: #fff;
    border-radius: 50%;
    border: 2px solid #DCE5E5;
    box-sizing: border-box;
    &::after {
      content: "";
      position: absolute;
      display: none;
      top: 3px;
      left: 3px;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: $orange; } } }

.check-wrap {
  display: block;
  position: relative;
  padding-left: 40px;
  padding-right: 20px;
  margin-bottom: 12px;
  @include fontFSC($goth-r, 16px);
  line-height: 25px;
  cursor: pointer;
  user-select: none;
  .check {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    &:checked ~ .check-label {
      &::after {
        display: block; } } }
  .check-label {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: transparent;
    border: 1px solid #fff;
    box-sizing: border-box;
    &::after {
      content: url('../img/checkout/check.svg');
      position: absolute;
      fill: $orange;
      display: none;
      top: -2px;
      left: 3px;
      width: 20px;
      height: 15px; } } }

.select-wrap {
  position: relative;
  cursor: pointer;
  svg {
    position: absolute;
    top: 13px;
    right: 9px; } }

.select {
  height: 34px;
  width: 124px;
  padding: 9px 10px;
  box-sizing: border-box;
  border: none;
  border-radius: 0;
  background-color: #646161;
  outline: none;
  appearance: none;
  @include fontFSC($goth-r, 14px);
  line-height: 16px; }

.menu-button {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 35px;
  height: 25px;
  background-color: transparent;
  span {
    transition: all 0.2s ease-in-out; }
  &:hover {
    span {
      background-color: $orange; } } }

.menu-button_active {
  position: relative;
  .menu-button__line {
    position: absolute;
    &:first-child {
      transform: rotate(-45deg);
      top: 11px; }
    &:nth-child(2) {
      display: none; }
    &:last-child {
      transform: rotate(45deg);
      bottom: 11px; } } }


.menu-button__line {
  display: block;
  height: 3px;
  border-radius: 2px;
  width: 100%;
  background-color: #fff; }

.slick-arrow {
  background-color: transparent;
  background-size: cover;
  width: 17px;
  height: 30px;
  border: none;
  color: transparent;
  position: absolute;
  top: 50%;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
  z-index: 20; }

.slick-next {
  background-image: url("data:image/svg+xml,%3Csvg width='18' height='30' viewBox='0 0 18 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M12.3111 15L0.15625 2.84518L2.51327 0.488159L17.0251 15L2.51327 29.5118L0.15625 27.1548L12.3111 15Z' fill='white'/%3E%3C/svg%3E%0A");
  right: 20px; }

.slick-prev {
  background-image: url("data:image/svg+xml,%3Csvg width='18' height='30' viewBox='0 0 18 30' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M5.69061 15L17.8454 27.1548L15.4884 29.5118L0.976562 15L15.4884 0.488159L17.8454 2.84518L5.69061 15Z' fill='white'/%3E%3C/svg%3E%0A");
  left: 20px; }
