@import "varialble";
@import "function";

.container__header {}

.header {
  padding: 25px 0;
  background-color: $bg-clr;
  @include tabletSmall {
    height: 92px;
    box-sizing: border-box; } }

.header_transparent {
  background-color: transparent; }

.header__logo {
  @include tabletSmall {
    svg, img {
      width: 92px;
      height: 42px; } } }

.header__menu {
  @include desktopSmall {
    display: none;
    position: absolute;
    top: 109px;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $bg-clr;
    z-index: 10;
    flex-direction: column;
    justify-content: flex-start;
    padding-top: 36px; }
  @include tabletSmall {
    top: 95px;
    padding-top: 18px; } }

.header__menu-item {
  @include fontFSC($goth-m, 14px);
  text-transform: uppercase;
  display: block;
  line-height: 52px;
  padding: 0 9px;
  transition: all 0.2s ease-in-out;
  &:hover {
    color: $orange; }
  @include desktopSmall {
    text-align: center;
    @include fontFSC($goth-m, 24px);
    line-height: 70px;
    height: 70px; }
  &:last-child {
    padding-right: 0; }
  &:first-child {
    padding-left: 0; }
  @include tabletSmall {
    font-size: 18px;
    line-height: 50px;
    height: 50px; } }

.header__mobile-item {
  display: none;
  &:nth-child(7) {
    margin-top: 46px;
    @include tabletSmall {
      margin-top: 20px; } }
  @include desktopSmall {
    display: block; } }

.header__menu-mobile-socials {
  display: none;
  @include desktopSmall {
    display: flex;
    margin: 60px auto 0;
    width: 120px;
    justify-content: space-between; }
  @include tabletSmall {
    width: 90px;
    padding: 0;
    margin-top: 45px; } }

.header__menu-mobile-soc {
  @include tabletSmall {
    svg {
      width: 25px; } } }

.header__menu-item_active {
  color: $orange; }

.header__small-items {}

.header__small-item {
  display: block;
  padding: 0 18px;
  transition: all 0.2s ease-in-out;
  &:last-child {
    padding-right: 0; }
  &:first-child {
    padding-left: 0; }
  &:hover {
    span {
      color: $orange; }
    svg path {
      fill: $orange; } }
  span {
    line-height: 52px;
    @include fontFSC($goth-m, 14px); }
  svg.small-item__img {
    width: 30px;
    path {
      transition: all 0.2s ease-in-out; } }
  @include desktopSmall {
    padding: 0 15px;
    &:first-child {
      display: none; }
    &:last-child {
      display: none; } } }

.header__menu-button {
  display: none;
  padding-right: 0;
  @include desktopSmall {
    display: block;
    div {
      margin-top: 16px; } }
  @include tabletSmall {
    div {
      margin-top: 10px; } } }

.header__arrow-after {
  display: inline-block;
  margin-left: 6px;
  transition: all 0.2s ease-in-out; }

.small-item__img {
  display: block;
  height: 100%;
  width: 100%;
  position: relative;
  z-index: 5; }

.small-item__cage {
  position: relative;
  &::after {
    content: "";
    display: block;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: #E68336;
    position: absolute;
    top: 20px;
    right: 12px;
    z-index: 1; }
  @include tabletSmall {
    &::after {
      top: 13px;
      right: 9px; } } }

.header-drop {
  display: flex;
  justify-content: space-between;
  cursor: pointer;
  position: relative;
  .header-drop__col-list {
    visibility: hidden; }
  &:hover {
    .header-drop__col-list {
      visibility: visible; }
    .header-drop__col-item-text {
      color: #fff; } } }

.header-drop__text {
  @include fontFSC($goth-b, 14px);
  line-height: 55px;
  margin-left: 10px; }

.header-drop__arrow {
  height: 57px;
  margin-left: 11px; }

.header-drop__col-list {
  visibility: hidden;
  position: absolute;
  z-index: 1;
  top: 40px;
  left: -17px;
  width: 167px;
  background-color: #2E2E2E;
  padding: 7px 0;
  box-sizing: border-box; }

.header-drop__col-item {
  display: flex;
  height: 40px;
  padding: 0 17px;
  box-sizing: border-box;
  &:hover {
    background: #191717; } }

.header-drop__col-item-img {
  height: 40px;
  line-height: 40px; }

.header-drop__col-item-text {
  @include fontFSC($goth-b, 14px);
  line-height: 44px;
  margin-left: 10px; }


.header-drop_lang {
  .header-drop__col-list {
    width: 80px;
    left: 18px; }
  .header-drop__col-item {
    padding: 0 10px; }
  .header-drop__col-item-text {
    margin-left: 0; } }
