@import "sass/varialble";
@import "sass/function";

.container__delivery {}

.delivery {}

.delivery__del-info {}

.del-info {
  background-image: url("../../img/delivery/info_bg.svg");
  background-repeat: no-repeat;
  background-position: 100% -22px;
  @include desktopBig {
    background-position: 80% -22px; }
  @include tabletSmall {
    background-position: 120% 150px;
    background-size: 100%; } }

.del-info__text {
  @include fontFSC($goth-r, 14px);
  margin-top: 15px;
  line-height: 13px;
  @include desktopSmall {
    line-height: 20px;
    width: 50%; }
  @include mobile {
    width: 100%; } }

.del-info__text_2 {
  margin-top: 35px;
  @include tabletSmall {
    width: 100%; } }

.del-info__text_bold {
  @include fontFSC($goth-b, 14px);
  @include desktopSmall {
    width: 50%; }
  @include tabletSmall {
    width: 100%; } }

.del-info__ways {
  display: flex;
  height: 50px;
  width: 540px;
  svg {
    display: block;
    height: 50px;
    &:nth-child(2) {
      height: 41px; } }
  @include desktopSmall {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 50%;
    height: auto; }
  @include tabletSmall {
    width: 100%; } }

.del-info__button {
  margin-top: 35px;
  @include desktopSmall {
    margin-top: 45px; } }

.delivery__yourself {
  margin-top: 39px; }

.yourself {
  height: 300px;
  background: url("../../img/delivery/yourself_bg.jpg") no-repeat top center;
  background-size: cover;
  padding: 0.01%;
  @include desktopSmall {
    height: 217px; }
  @include mobile {
    height: 217px; } }

.yourself__heading {
  text-align: center;
  margin: 53px auto 0;
  width: 458px;
  @include tabletSmall {
    width: 100%; }
  @include mobile {
    margin-top: 40px;
    font-size: 20px;
    line-height: 25px;
    width: 274px; } }

.yourself__text {
  @include fontFSC($goth-r, 24px);
  text-align: center;
  width: 674px;
  margin: 30px auto 0;
  line-height: 35px;
  @include desktopSmall {
    margin-top: 25px;
    font-size: 18px;
    line-height: 25px;
    width: 453px; }
  @include tabletSmall {
    width: 100%; }
  @include mobile {
    font-size: 18px;
    line-height: 25px;
    margin-top: 10px; } }

.yourself__text_red {
  @include fontFSC($goth-m, 35px, #E34035);
  @include desktopSmall {
    font-size: 20px;
    line-height: 25px; } }

.delivery__zone {}

.zone {
  background: $bg-clr;
  padding: 0.01%; }

.zone__heading {
  margin-top: 50px;
  text-align: center;
  @include tabletSmall {
    margin-top: 25px; } }

.zone__z-map {
  margin-top: 36px; }

.z-map {}

.z-map__img {
  width: 100%; }

.z-map__description {
  margin-top: 50px;
  margin-bottom: 60px;
  @include desktopSmall {
    flex-wrap: wrap;
    margin-top: 35px;
    margin-bottom: 30px; } }

.z-map__item {
  @include fontFSC($goth-m, 14px);
  display: flex;
  width: 263px;
  div {
    flex-shrink: 1; }
  &::before {
    content: "";
    display: inline-block;
    flex-shrink: 0;
    height: 20px;
    width: 20px;
    vertical-align: middle;
    margin-right: 10px; }
  &:nth-child(1)::before {
    background: #4A5DC3; }
  &:nth-child(2)::before {
    background: #E68336; }
  &:nth-child(3)::before {
    background: #5FB709; }
  &:nth-child(4)::before {
    background: #E34035; }
  @include desktopSmall {
    margin-bottom: 25px;
    width: 287px;
    margin-left: 92px;
    &:nth-child(2n+1) {
      margin-left: 0; }
    &::before {
      width: 40px; } }
  @include tabletSmall {
    margin-left: 0; } }

.delivery__payment {
  margin-top: 60px;
  @include desktopSmall {
    margin-top: 25px; } }

.payment {
  height: 327px;
  @include desktopSmall {
    height: 225px; }
  @include mobile {
    height: auto;
    padding-bottom: 25px; } }

.payment__heading {
  text-align: center;
  margin-top: 60px;
  @include desktopSmall {
    margin-top: 25px; } }

.payment__ways {
  margin: 60px auto 0;
  width: 580px;
  @include desktopSmall {
    margin-top: 30px; }
  @include tabletSmall {
    width: 100%; }
  @include mobile {
    flex-direction: column;
    margin-top: 0; } }

.payment__way {
  width: 255px;
  @include mobile {
    margin: 30px auto 0; } }

.payment__w-img {
  display: block;
  margin: 0 auto; }

.payment__w-text {
  margin-top: 25px;
  @include fontFSC($goth-r, 14px);
  line-height: 20px;
  text-align: center; }
