@import "sass/varialble";
@import "sass/function";

.container__cart {
  margin-top: 21px; }

.cart {
  display: flex;
  margin-bottom: 100px;
  @include desktopBig {
    justify-content: space-between; }
  @include desktopSmall {
    flex-direction: column; } }

.cart__prd-list {
  flex: none;
  @include desktopBig {
    flex: 1; } }

.prd-list {
  width: 848px;
  min-height: 390px;
  border-collapse: collapse;
  border-spacing: 0 50px;
  @include desktopSmall {
    width: 100%; } }

.prd-list__head {
  height: 80px;
  border: 1px solid #E68336;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  padding: 0 65px 0 220px;
  @include desktopSmall {
    height: 60px; }
  @include tabletSmall {
    display: none; } }

.prd-list__head-item {
  @include fontFSC($goth-m, 16px, $clr);
  line-height: 80px;
  &:nth-child(2) {
    width: 100px; }
  @include desktopSmall {
    font-size: 14px;
    line-height: 60px; } }

.prd-list__body {
  padding: 15px 20px;
  box-sizing: border-box;
  border: 1px solid #E68336;
  @include desktopSmall {
    padding: 25px 33px; }
  @include tabletSmall {
    width: 370px;
    margin: 0 auto; }
  @include mobile {
    width: 100%;
    margin: 0; } }

.prd-list__row {
  height: 150px;
  @include desktopSmall {
    height: 119px; }
  @include tabletSmall {
    height: 420px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin: 25px auto 0;
    max-width: 269px;
    &:first-child {
      margin-top: 0; } } }

.prd-list__column {
  padding: 0;
  @include tabletSmall {
    order: 2;
    margin-top: 15px;
    display: flex;
    justify-content: space-between;
    &:nth-child(6) {
      order: 1;
      margin-top: 0; }
    &:nth-child(3), &:nth-child(4), &:nth-child(5) {
      margin-top: 25px; } } }

.prd-list__mobile-title {
  display: none;
  @include tabletSmall {
    display: block;
    @include fontFSC($goth-m, 14px, $clr);
    line-height: 20px; } }

.prd-list__col-img {
  @include desktopSmall {
    height: 119px; }
  @include tabletSmall {
    width: 269px;
    height: 198px;
    object-fit: cover; } }

.prd-list__col-name {
  @include fontFSC($goth-m, 16px, $clr);
  line-height: 150px;
  @include desktopSmall {
    line-height: 119px; }
  @include tabletSmall {
    line-height: 20px;
    font-size: 14px; } }

.prd-list__col-quantity {
  height: 34px;
  width: 104px;
  background-color: #646161;
  margin-top: 58px;
  @include desktopSmall {
    margin-top: 42px; }
  @include tabletSmall {
    margin-top: 0;
    width: 92px;
    height: 30px; } }

.prd-list__plus {
  @include fontFSC($goth-r, 14px);
  line-height: 34px;
  width: 40px;
  text-align: center;
  cursor: pointer;
  @include tabletSmall {
    line-height: 31px; } }

.prd-list__qnt-value {
  @include fontFSC($goth-r, 14px);
  line-height: 34px;
  @include tabletSmall {
    line-height: 31px; } }

.prd-list__price {
  @include fontFSC($goth-b, 24px);
  line-height: 150px;
  @include desktopSmall {
    line-height: 119px; }
  @include tabletSmall {
    line-height: 20px;
    font-size: 14px; } }

.prd-list__cross {
  width: 17px;
  height: 17px;
  margin-top: 67px;
  @include desktopSmall {
    margin-top: 50px; }
  @include tabletSmall {
    display: block;
    margin-top: 0;
    margin-left: auto; } }

.cart__cart-checkout {
  flex: 1;
  margin-left: 30px;
  @include desktopBig {
    flex: 0; }
  @include desktopSmall {
    margin-top: 45px;
    margin-left: 0; }
  @include tabletSmall {
    margin-top: 25px; } }

.cart-checkout {}

.cart-checkout__wrap {
  @include desktopSmall {
    display: flex;
    justify-content: space-between; }
  @include tabletSmall {
    justify-content: flex-start;
    flex-direction: column; } }

.cart-checkout__first-col {
  @include desktopSmall {
    width: 286px; }
  @include tabletSmall {
    width: 100%; } }

.cart-checkout__heading {
  @include fontFSC($goth-m, 20px);
  line-height: 20px; }

.cart-checkout__sub-heading {
  @include fontFSC($goth-m, 16px);
  line-height: 20px;
  margin-top: 25px; }

.cart-checkout__radio {
  margin-top: 18px; }

.cart-checkout__selections {
  @include desktopSmall {
    width: 286px; }
  @include tabletSmall {
    width: 100%; } }

.cart-checkout__delivery {
  max-width: 482px;
  height: 196px;
  border: 1px solid #E34035;
  padding: 41px 20px;
  box-sizing: border-box;
  margin-top: 25px;
  svg {
    margin-top: 19px;
    margin-right: 16px; }
  @include desktopSmall {
    width: 347px;
    padding: 34px 25px; }
  @include tabletSmall {
    width: 100%;
    max-width: 100%;
    padding: 34px 19px;
    height: auto; } }

.cart-checkout__delivery-text {
  @include fontFSC($goth-m, 16px, #E34035);
  line-height: 20px;
  width: 220px;
  @include desktopSmall {
    width: 100%; } }

.cart-checkout__date {}

.cart-checkout__footer {
  margin-top: 37px;
  border-top: 1px solid #fff;
  padding: 26px 0 0;
  @include desktopSmall {
    width: 286px; }
  @include tabletSmall {
    width: 100%; } }

.cart-checkout__foot-sum {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 16px; }

.cart-checkout__foot-item {
  margin-right: 15px;
  @include fontFSC($goth-r, 16px);
  line-height: 15px; }

.cart-checkout__foot-value {
  @include fontFSC($goth-r, 16px);
  line-height: 15px; }

.cart-checkout__foot-item_bold {
  @include fontFSC($goth-b, 16px);
  line-height: 15px; }

.cart-checkout__foot-value_bold {
  @include fontFSC($goth-b, 16px);
  line-height: 15px; }

.cart-checkout__button {
  margin-top: 45px;
  @include tabletSmall {
    margin: 45px auto; }
  @include mobile {
    width: 100%; } }
